import Vue from 'vue'
import Router from 'vue-router'

// Containers
const LoginContainer = () => import('@/containers/LoginContainer')
const TheContainer = () => import('@/containers/TheContainer')
const PrintContainer = () => import('@/containers/PrintContainer')

const UserManagement = () => import('@/views/user-management/Index')
const Users = () => import('@/views/user-management/users/All')
const Roles = () => import('@/views/user-management/roles/All')

const MyAccount = () => import('@/views/MyAccount')

const TicketCreate = () => import('@/views/ticket/TicketCreate')
const TicketPrint = () => import('@/views/ticket/TicketPrint')
const TicketScan = () => import('@/views/ticket/TicketScan')
const TicketList = () => import('@/views/ticket/TicketList')


const Dashboard = () => import('@/views/Dashboard2')
const Icons = () => import('@/views/icons/CoreUIIcons')
const DashboardDefault = () => import('@/views/DashboardDefault')
const MasterList = () => import('@/views/voters/MasterList')
const PerBarangay = () => import('@/views/voters/PerBarangay')


const QrGenerator = () => import('@/views/qr-generator/Index')
Vue.use(Router)

export default new Router({
  base:'/',
  //base:'/franco_time_log/build/',
  mode: 'history', // https://router.vuejs.org/api/#mode
  linkActiveClass: 'active',
  scrollBehavior: () => ({ y: 0 }),
  routes: configRoutes()
})

function configRoutes () {
  return [

      {
          path: '/qr-generator/:id',
          name: 'QR Generator',
          component: QrGenerator
      },
      {
          path: '/login',
          name: 'Login',
          component: LoginContainer
      },
      {
          path: '/',
          redirect: '/dashboard',
          name: 'Home',
          component: TheContainer,
          children: [
              // {
              //     path: '/ticket-create',
              //     name: 'Ticket Create',
              //     component: TicketCreate
              // },
              // {
              //     path: '/ticket-scan',
              //     name: 'Ticket Scan',
              //     component: TicketScan
              // },
              // {
              //     path: '/ticket-list',
              //     name: 'Ticket List',
              //     component: TicketList
              // },
              {
                  path: '/icons',
                  name: 'Icons',
                  component: Icons
              },
              {
                  path: '/dashboard-default',
                  name: 'DashboardDefault',
                  component: DashboardDefault
              },
              {
                  path: '/dashboard',
                  name: 'Dashboard',
                  component: Dashboard
              },
              {
                  path: '/master-list',
                  name: 'Master List',
                  component: MasterList
              },
              {
                  path: '/per-barangay',
                  name: 'Per Barangay',
                  component: PerBarangay
              },
              {
                  path: '/ticket-scan',
                  name: 'Ticket Scan',
                  component: TicketScan
              },
              {
                  path: '/ticket-list',
                  name: 'Ticket List',
                  component: TicketList
              },
              {
                  path: '/my-account',
                  name: 'My Account',
                  component: MyAccount
              },
              {
                  path: '/user-management',
                  redirect: '/user-management/users',
                  name: 'User Management',
                  component: UserManagement,
                  children: [
                      {
                          path: '/user-management/users',
                          name: 'Users',
                          component: Users
                      },
                      {
                          path: '/user-management/roles',
                          name: 'Roles',
                          component: Roles
                      }
                  ]
              },


          ]
      },
      {
          path: '/',
          redirect: '/ticket-print',
          name: 'Print',
          component: PrintContainer,
          children: [
              {
                  path: '/ticket-print/:id',
                  name: 'Ticket Print',
                  component: TicketPrint
              },


          ]
      },
      
  ]
}

